.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
  }
  
  h2 {
    margin-bottom: 20px;
    color: aliceblue;
  }
  
  form {
    display: flex;
    flex-direction: column;
    width: 300px;
  }
  
  input {
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 3px;
    border: none;
    background-color: #333333;
    color: white;
  }
  
  button {
    padding: 10px;
    margin-top: 10px;
    border-radius: 3px;
    border: none;
    background-color: #e50914;
    color: white;
    font-weight: bold;
    cursor: pointer;
  }
  