.row{
    margin-left: 20px;
    color: #fff;

}

.posters{
    display: flex;
    padding: 30px;
    overflow-x: scroll;
    overflow-y: hidden;
}

.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    max-height:250px;
    margin-right: 10px;
    cursor: pointer;
}

.smallPoster{
    max-height: 150px;
    margin-right: 10px;
    cursor: pointer;
}

.poster:hover{
    transform: scale(1.2);
}

.smallPoster:hover{
    transform: scale(1.2);
}

.video {
    width:  auto;
    height: auto;
    cursor: pointer;
  }

  .title{
    font-size: 1rem;
  }